<template>
  <TabelaComp
  tituloCard="Saida"
  :items="getItemsSaida()"
  :headers="headers" />
</template>

<script>
import { mapGetters } from 'vuex'
import TabelaComp from './TabelaComp'

export default {
    name: 'SaidaComp',

    components: {
      TabelaComp
    },

    data: () => ({
      headers: [
        { text: 'Código', value: 'id' },
        { text: 'Descrição', value: 'divida.descricao' },
        { text: 'Valor Parcela', value: 'valor_parcela' },
        { text: 'Quantidade de Parcelas', value: 'divida.num_parcelas' },
        { text: 'Status', value: 'status_pago' },
        { text: 'Açôes', value: 'actions' },
      ]
    }),

    methods: {
      ...mapGetters([
        'getItemsSaida',
      ])
    }

}
</script>
