<template>
  <div>
    <!-- Menu -->
    <v-card>
      <v-card-title>
        Menu

        <v-spacer></v-spacer>

        <v-menu
          ref="menu"
          v-model="menu"
          :close-on-content-click="false"
          :return-value.sync="date"
          transition="scale-transition"
          offset-y
          max-width="90%"
          min-width="auto"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="date"
              append-icon="mdi-filter"
              readonly
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker
            v-model="date"
            type="month"
            no-title
            scrollable
            locale="pt-br"
          >
            <v-spacer></v-spacer>
            
            <v-btn text color="primary" @click="menu = false">
              Cancelar
            </v-btn>
            <v-btn text color="primary" @click="$refs.menu.save(date)">
              OK
            </v-btn>
          </v-date-picker>
        </v-menu>

        <v-spacer></v-spacer>

        <v-dialog transition="dialog-top-transition" max-width="600">
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="teal accent-2" v-bind="attrs" v-on="on">
              Adicionar Entrada/Saida
            </v-btn>
          </template>

          <template v-slot:default="dialog">
            <v-card>
              <v-toolbar dark>
                Adicionar
              </v-toolbar>

              <v-card-text>
                <v-form ref="form">
                  <!-- Descrição -->
                  <v-text-field
                    v-model.trim="form.descricao"
                    :counter="100"
                    label="Descrição"
                    :rules="[() => !!form.descricao
                                   || 'A descrição é obrigatória',
                             () => form.descricao.length<=100
                                   || 'A descrição deve conter no máximo 100 caracteres',]"
                    required
                  ></v-text-field>
              
                  <!-- Data -->
                  <v-text-field
                    v-model.trim="form.data"
                    type="month"
                    label="Data"
                    :rules="[v => !!v || 'A data é obrigatória']"
                    required
                  ></v-text-field>
              
                  <!-- Tipo -->
                  <v-select
                    v-model.trim="form.tipo"
                    :items="tipos"
                    label="Tipo"
                    :rules="[v => !!v || 'O tipo é obrigatório']"
                    required
                  ></v-select>
              
                  <!-- Valor Total -->
                  <v-text-field
                    v-model.trim="form.valor_total"
                    type="number"
                    label="Valor Total"
                    :rules="[v => !!v || 'O valor total é obrigatório']"
                    required
                  ></v-text-field>

                  <!-- Quantidade de Parcelas -->
                  <v-text-field
                    v-model.trim="form.num_parcelas"
                    type="number"
                    label="Quantidade de Parcelas"
                    :rules="[v => !!v || 'A quantidade de parcelas é obrigatória',
                             v => v>0 || 'O numeros de parcelas deve ser maior do que 0']"
                    required
                  ></v-text-field>
                </v-form>
              </v-card-text>

              <v-card-actions class="justify-end">
                <v-btn
                  text
                  @click="dialog.value=false"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  text
                  @click="salvar(dialog)"
                >
                  Salvar
                </v-btn>
              </v-card-actions>
            </v-card>
          </template>
        </v-dialog>
      </v-card-title>
    </v-card>

    <br>

    <!-- Painel Valores -->
    <PainelValoresComp />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import PainelValoresComp from "./PainelValoresComp";

export default {
  components: {
    PainelValoresComp
  },

  data: () => ({
    menu: false,
    dialog: false,
    date: new Date().toISOString().substr(0, 7),
    tipos: ['entrada', 'saida'],
    form: {
      'descricao': '',
      'data': new Date().toISOString().substr(0, 7),
      'num_parcelas': 1
    },
  }),

  methods: {
    ...mapActions([
      "dividasAnoMesAction",
      "adicionarAction"
    ]),

    ...mapGetters(["getDateState"]),

    salvar(dialog) {
      if (this.$refs.form.validate()) {
        this.adicionarAction(this.form);
        this.form = {
          'descricao': '',
          'data': new Date().toISOString().substr(0, 7),
          'num_parcelas': 1
        },
        dialog.value=false;
      }
    }
  },

  mounted() {
    this.dividasAnoMesAction(this.date);
    this.$store.state.date_state=this.date;
  },

  watch: {
    date: function () {
      this.dividasAnoMesAction(this.date);
      this.$store.state.date_state=this.date;
    },
  },
};
</script>
