<template>
  <v-card
    dark
  >
    <v-card-title>
      {{ tituloCard }}

      <v-spacer></v-spacer>

      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Buscar"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>

    <v-data-table
      height="100%"
      :headers="headers"
      :items="items"
      :search="search"
      :items-per-page="10"
      class="elevation-1"
    >
      <!-- status -->
      <template v-slot:[`item.id`]="{ item }">
        <span
          :class="{'text--green': item.status_pago === 'Pago!' || item.status_pago === 'Recebido!'}"
        >
          {{ item.id }}
        </span>
      </template>

      <!-- descricao -->
      <template v-slot:[`item.divida.descricao`]="{ item }">
        <span
          :class="{'text--green': item.status_pago === 'Pago!' || item.status_pago === 'Recebido!'}"
        >
          {{ item.divida.descricao }}
        </span>
      </template>

      <!-- valor parcela -->
      <template v-slot:[`item.valor_parcela`]="{ item }">
        <span
          :class="{'text--green': item.status_pago === 'Pago!' || item.status_pago === 'Recebido!'}"
        >
          {{ item.valor_parcela }}
        </span>
      </template>

      <!-- Quantidade de parcelas -->
      <template v-slot:[`item.divida.num_parcelas`]="{ item }">
        <span
          :class="{'text--green': item.status_pago === 'Pago!' || item.status_pago === 'Recebido!'}"
        >
          {{ item.divida.num_parcelas }}
        </span>
      </template>

      <!-- status -->
      <template v-slot:[`item.status_pago`]="{ item }">
        <v-chip
          :color="item.status_pago==='Pago!' ||
                  item.status_pago==='Recebido!'
                  ? 'green' : 'grey'"
          @click="atualizarStatusAction(item)"
        >
          {{ item.status_pago }}
        </v-chip>
      </template>

      <template v-slot:[`item.actions`]="{ item }">
        
        <!-- Atualizar -->
        <v-dialog transition="dialog-top-transition" max-width="600">
          <template v-slot:activator="{ on, attrs }">
            <v-icon
              small
              color="teal accent-2"
              class="mr-2"
              v-bind="attrs"
              v-on="on"
              @click="formatar_form(item)"
            >
              mdi-pencil
            </v-icon>
            
          </template>
  
          <template v-slot:default="dialog">
            <v-card>
              <v-toolbar dark>
                Atualizar
              </v-toolbar>
              <br>
  
              <v-card-text>
                <v-form ref="form">
                  <!-- Descrição -->
                  <v-text-field
                    v-model="form.divida.descricao"
                    :counter="100"
                    label="Descrição"
                    :rules="[() => !!form.divida.descricao
                                   || 'A descrição é obrigatória',
                             () => form.divida.descricao.length<=100
                                   || 'A descrição deve conter no máximo 100 caracteres',]"
                    required
                  ></v-text-field>
                  
                  <!-- Data -->
                  <v-text-field
                    type="month"
                    label="Data"
                    :value="formata_data(form.data)"
                    :rules="[v => !!v || 'A data é obrigatória']"
                    disabled
                  ></v-text-field>
              
                  <!-- Tipo -->
                  <v-select
                    v-model="form.divida.tipo"
                    :items="tipos"
                    label="Tipo"
                    :rules="[v => !!v || 'O tipo é obrigatório']"
                    required
                  ></v-select>
              
                  <!-- Valor Parcela -->
                  <v-text-field
                    v-model="form['valor_parcela']"
                    type="number"
                    label="Valor Parcela"
                    :rules="[v => !!v || 'O valor da parcela é obrigatório']"
                  ></v-text-field>
  
                  <!-- Quantidade de Parcelas -->
                  <v-text-field
                    v-model="form['divida']['num_parcelas']"
                    type="number"
                    label="Quantidade de Parcelas"
                    :rules="[v => !!v || 'A quantidade de parcelas é obrigatória',
                             v => v>0 || 'O numeros de parcelas deve ser maior do que 0']"
                    disabled
                  ></v-text-field>
                </v-form>
              </v-card-text>
  
              <v-card-actions class="justify-end">
                <v-btn
                  text
                  @click="dialog.value = false"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  text
                  @click="atualizar(dialog)"
                >
                  Atualizar
                </v-btn>
              </v-card-actions>
            </v-card>
          </template>
        </v-dialog>


        <!-- deletar -->
        <v-dialog transition="dialog-top-transition" max-width="600">
          <template v-slot:activator="{ on, attrs }">
            <v-icon
              small
              class="mr-2"
              color="teal accent-2"
              v-bind="attrs"
              v-on="on"
            >
              mdi-delete
            </v-icon>
            
          </template>
  
          <template v-slot:default="dialog">
            <v-card>
              <v-toolbar dark>
                Remover
              </v-toolbar>
  
              <v-card-text>
                <br>
                <h1>
                  Tem certeza que deseja remover?
                </h1>
                <br>
                <h2 v-if="item.divida.num_parcelas > 1">
                  Obs.: Será removido as {{ item.divida.num_parcelas }} parcelas da {{ item.divida.tipo }} de código {{ item.id }}
                </h2>
              </v-card-text>
  
              <v-card-actions class="justify-end">
                <v-btn text @click="dialog.value = false">
                  Cancelar
                </v-btn>
                <v-btn text @click="dialog.value = false, removerAction(item)">
                  Remover
                </v-btn>
              </v-card-actions>
            </v-card>
          </template>
        </v-dialog>
      </template>
    </v-data-table>
  </v-card>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  name: "TabelaComp",

  data: () => ({
    search: "",
    form: {},
    tipos: ['entrada', 'saida'],
    teste: 0
  }),

  props: {
    tituloCard: String,
    items: Array,
    headers: Array,
  },

  methods: {
    ...mapActions([
      'removerAction',
      'atualizarAction',
      'atualizarStatusAction'
    ]),

    atualizar(dialog) {
      if (this.$refs.form.validate()) {
        this.atualizarAction(this.form);
        dialog.value=false;
      }
    },

    formata_data(data) {
      const data_formatada = data.slice(6,)+'-'+data.slice(3,5);
      return data_formatada;
    },

    formatar_form(item) {
      this.form = {
        'id': item['id'],
        'data': item['data'],
        'valor_parcela': item['valor_parcela'].slice(3).replace('.', '').replace(',', '.'),
        'divida': item['divida']
      }
    }
  }
};
</script>

<style>
  .elevation-1 tbody tr:nth-of-type(odd) {
    background-color: rgb(0 ,0, 1);
  }

  .v-data-table-header {
    background-color: #004D40;
  }

  .text--green {
    color: #4CAF50;
  }
</style>