import 'es6-promise/auto'

import Vue from 'vue'
import Vuex from 'vuex'
import api from '@/services/api'


Vue.use(Vuex)


const store = new Vuex.Store({
    state:{
        date_state: new Date().toISOString().substr(0, 7),
        entrada: [],
        saida: [],
        valores: {}
    },

    getters: {
        getDateState(self) {
            return self.date_state;
        },

        getItemsSaida(self) {
            return self.saida;
        },

        getItemsEntrada(self) {
            return self.entrada;
        },

        getValores(self) {
            return self.valores;
        }
    },

    actions: {
        dividasAnoMesAction({ commit }, anoMes) {
            api.get('parcelas/' + anoMes)
            .then(( response ) => {
                commit('atualizarEntradaSaidaMutation', response.data)
            })
            .catch((error) => {
                console.log(error);
            });
        },

        adicionarAction({ commit }, form) {
            api.post('dividas/', form)
            .then((response) => {
                commit('adicionarMutation', response.data.data[0])
            })
            .catch((error) => {
                console.log(error);
            });
        },

        atualizarAction({ commit }, form) {
            api.put('dividas/', form)
            .then((response) => {
                commit('atualizarEntradaSaidaMutation', response.data)
            })
            .catch((error) => {
                console.log(error);
            });
        },

        atualizarStatusAction({ commit }, item) {
            api.put('parcelas/status/', item)
            .then((response) => {
                commit('atualizarEntradaSaidaMutation', response.data)
            })
            .catch((error) => {
                console.log(error);
            });
        },

        removerAction({ commit }, item) {
            api.delete('dividas/' + item.divida.id)
            .then(() => {
                commit('removerMutation', item.id)
            })
            .catch((error) => {
                console.log(error);
            });
        },

        painelValoresAction({ commit }, date) {
            api.get('painel-valores/' + date)
            .then((response) => {
                commit('painelValoresMutation', response.data)
            })
            .catch((error) => {
                console.log(error);
            });
        }
    },

    mutations: {
        atualizarEntradaSaidaMutation(state, data) {
            state.entrada = data.data.entrada;
            state.saida = data.data.saida;
        },

        adicionarMutation(state, data) {
            const mes = state.date_state.slice(5,7)
            const ano = state.date_state.slice(0, 4)
            const data_referencia = '01/'+mes+'/'+ano

            if (data_referencia === data['data']) {
                if (data['divida']['tipo'] === 'entrada') {
                        state.entrada.push(data)
                } else {
                        state.saida.push(data)
                }
            }
        },

        removerMutation(state, id_parcela) {
            if (state.entrada.find(elemento => elemento.id === id_parcela)) {
                state.entrada = state.entrada.filter(item => item.id != id_parcela)
            }else {
                state.saida = state.saida.filter(item => item.id != id_parcela)
            }     
        },

        painelValoresMutation(state, valores) {
            state.valores = valores.data
        }
    }

})

export default store;